/* Файл elements/menu-button.less */
.menu-button {
  position: relative;
  display: block;
  padding: 0;
  border: none;
  background-color: #FE7865;
  width: 50px;
  height: 50px;
  z-index: 3;
  cursor: pointer;
}
.menu-button:hover,
.menu-button:focus {
  background-color: #F17D4B;
}
.menu-button::before {
  content: "";
  width: 22px;
  height: 1px;
  background-color: #ffffff;
  position: absolute;
  top: 0;
  bottom: 3px;
  left: 0;
  right: 0;
  margin: auto;
  box-shadow: 0 7px 0 0 #ffffff, 0 -6px 0 0 #ffffff;
}
.menu-button--no-js {
  display: none;
}
.menu-button--nav-open {
  background-color: transparent;
}
.menu-button--nav-open:hover,
.menu-button--nav-open:focus {
  background-color: transparent;
}
.menu-button--nav-open::before,
.menu-button--nav-open::after {
  content: "";
  width: 30px;
  height: 1px;
  background-color: rgba(20, 21, 23, 0.2);
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  box-shadow: none;
}
.menu-button--nav-open::before {
  transform: rotate(45deg);
}
.menu-button--nav-open::after {
  transform: rotate(-45deg);
}
