  height: 568px;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  z-index: 3;
  padding-top: 100px;
  display: none;
  max-width: 720px;
}
.buy.open {
  display: block;
}
