  margin: 0;
  margin-bottom: 50px;
  padding: 0;
  font-weight: 600;
  font-size: 25px;
  line-height: 170%;
  text-align: center;
  color: #48494D;
}
.buy__button-close {
  width: 22px;
  height: 22px;
  margin: 0;
  padding: 0;
  background-color: transparent;
  position: absolute;
  top: 20px;
  right: 20px;
  border: none;
  z-index: 11;
}
.buy__button-close:hover,
.buy__button-close:focus {
  background-color: transparent;
}
.buy__button-close::before,
.buy__button-close::after {
  content: "";
  width: 30px;
  height: 1px;
  background-color: rgba(20, 21, 23, 0.2);
  position: absolute;
  top: 0;
  bottom: 0;
  left: -4px;
  right: 0;
  margin: auto;
  box-shadow: none;
}
.buy__button-close::before {
  transform: rotate(45deg);
}
.buy__button-close::after {
  transform: rotate(-45deg);
}
